(function() {
  angular.module('morano2017', ['ngAnimate', 'ngTouch', 'ngSanitize', 'ui.router', '720kb.socialshare']);

}).call(this);


(function() {
  angular.module('morano2017').controller('MainQuoteController', function($state, $stateParams, sentence) {
    this.sentence = sentence;
    this.url = $state.href('main.quote', $stateParams, {
      absolute: true
    });
    this.medias = ["J'aime lire", "Le chasseur français", "Production laitière magazine", "Santiag magazine", "L'Express", "Le Point", "Valeurs actuelles", "L'Usine nouvelle", "Mon quotidien", "Le Monde", "AFP", "Le Figaro"];
    this.media = this.medias[Math.floor(Math.random() * this.medias.length)];
    this.tomorrow = new Date(new Date().getTime() + 24 * 60 * 60 * 1000);
    return null;
  });

}).call(this);


(function() {
  angular.module('morano2017').controller('MainController', function($state, facts) {
    this.bg = 'nadine-' + Math.floor(Math.random() * 5);
    this.generate = function() {
      var i, idx, key, p, params, results;
      this.bg = 'nadine-' + Math.floor(Math.random() * 5);
      params = {
        p1: null,
        p2: null,
        p3: null
      };
      results = [];
      while (params.p1 === params.p2 && params.p2 === params.p3) {
        for (p = i = 1; i <= 3; p = ++i) {
          key = 'p' + p;
          params[key] = null;
          while (params[key] === null) {
            idx = Math.floor(Math.random() * facts[key].length);
            if (facts[key][idx] !== '') {
              params[key] = idx;
            }
          }
        }
        results.push($state.go('main.quote', params));
      }
      return results;
    };
    return null;
  });

}).call(this);


(function() {
  angular.module('morano2017').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);


(function() {
  angular.module('morano2017').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $urlRouterProvider.otherwise('/');
    return $stateProvider.state('main', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainController',
      controllerAs: 'main',
      resolve: {
        facts: function($http) {
          return $http.get('assets/facts.json').then(function(r) {
            return r.data;
          });
        }
      }
    }).state('main.quote', {
      url: ':p1::p2::p3',
      templateUrl: 'app/main/quote/quote.html',
      controller: 'MainQuoteController',
      controllerAs: 'quote',
      resolve: {
        sentence: function($stateParams, facts) {
          var p;
          p = $stateParams;
          return [facts.p1[p.p1], facts.p2[p.p2], facts.p3[p.p3]].join('');
        }
      }
    });
  });

}).call(this);


(function() {
  angular.module('morano2017').constant('moment', moment);

}).call(this);


(function() {
  angular.module('morano2017').config(function($logProvider) {
    'ngInject';
    return $logProvider.debugEnabled(true);
  });

}).call(this);


